
.sidedrawer{
    position: fixed;
    width: 200px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: whitesmoke;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;

}

@media (min-width: 500px){
    .sidedrawer{
        display: none;
    }
}

.open{
    transform: translateX(0);
}

.closed{
    transform: translateX(-100%);
}

.logo{
    height: 11%;
    margin-bottom: 20px;
}